import React from 'react';
import { NavLink as Link, Switch, Route } from 'react-router-dom';
import { HelloWorld } from '@nerdter/nerdter-ui';

// import child components
import { Counter } from '../counter';
import { Post } from '../post';

// export entry application component
export class App extends React.Component {
    constructor() {
        console.log('App.constructor()');
        super();
    }

    // render view
    render() {
        console.log('App.render()');

        return (
            <div className='container'>
                {/* navigation */}
                <nav>
                    <ul>
                        <li class="fullwidth"><a href="#">Home</a></li>
                        <li>

                            <Link
                                className='navigation__link'
                                activeClassName='navigation__link--active'
                                to='/'
                                exact={true}
                            >Counter</Link>
                        </li>
                        <li><Link
                            className='ui-app__navigation__link'
                            activeClassName='ui-app__navigation__link--active'
                            to='/post'
                            exact={true}
                        >Post</Link></li>
                        <li><a href="#">Contact</a></li>
                    </ul>
                </nav>

                <Switch>
                    <Route
                        path='/'
                        exact={true}
                        render={() => {

                            return (<div className="grid-6">
                                <div class="col-4"><Counter name='HEADER?' /></div>
                                <HelloWorld/>
                            </div>)
                        }}
                    />

                    <Route
                        path='/post'
                        exact={true}
                        component={Post}
                    />
                </Switch>







            </div>
        );
    }
}